<script lang="ts">;
;
;
;
import { spring } from "svelte/motion";
import project, { player } from "@app/stores/project";
import VStack from "@components/layout/VStack.svelte";
import PlaylistTrack from "./Track.svelte";
let cursorPos = 0;
let playlistWidth = 300;
let viewRegion = [0, 20];
let secWidth;
let snap;
let movedClip = null;
let pointerStart;
let animatedViewRegion = spring(viewRegion, {
    stiffness: 0.25,
    damping: 1.0,
});
export function scrollBy(xdelta) {
    const span = viewRegion[1] - viewRegion[0];
    viewRegion[0] = Math.max(0, viewRegion[0] + xdelta / secWidth);
    viewRegion[1] = viewRegion[0] + span;
}
$: $animatedViewRegion = viewRegion;
$: secWidth =
    playlistWidth / ($animatedViewRegion[1] - $animatedViewRegion[0]);
$: {
    // snap is 1 bar by default
    snap = $project.barsToTime(1);
    for (let i = 0; i < 4 && (snap / 2) * secWidth >= 20; i++) {
        snap /= 2;
    }
}
$: if ($player.playing) {
    const update = () => {
        if ($player.playing) {
            cursorPos = ($player.currentTime - $animatedViewRegion[0]) * secWidth;
            requestAnimationFrame(update);
        }
    };
    update();
}
else {
    cursorPos = ($player.startCursor - $animatedViewRegion[0]) * secWidth;
}
function initPlaylistWidth(node) {
    playlistWidth = node.getBoundingClientRect().width;
}
function handleWheel(e) {
    if (e.ctrlKey) {
        e.preventDefault();
        const rect = this.getBoundingClientRect();
        const wheelDelta = Math.sign(e.deltaY);
        const scaling = 1.0 + wheelDelta * 0.1;
        const aimedTime = $animatedViewRegion[0] + (e.clientX - rect.left) / secWidth;
        for (let i = 0; i < 2; i++) {
            const t = viewRegion[i];
            const d = t - aimedTime;
            viewRegion[i] = Math.max(0, aimedTime + d * scaling);
        }
    }
}
function handleGlobalKeyDown(e) {
    if (e.key === "Alt") {
        e.preventDefault();
    }
}
function handleGlobalKeyUp(e) {
    if (e.key === "Alt") {
        e.preventDefault();
    }
}
</script>

<style lang="scss">.playlist {
  position: relative;
  overflow: hidden;
}
.playlist .cursor {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 1px;
  background: var(--color-accent);
  opacity: 0.5;
  transform: translateX(var(--x));
}</style>

<svelte:window
  on:keydown={handleGlobalKeyDown}
  on:keyup={handleGlobalKeyUp}
  on:pointerup={(e) => {
    if (e.button !== 0) {
      return;
    }

    movedClip = null;
  }} />

<div
  class="playlist"
  use:initPlaylistWidth
  bind:clientWidth={playlistWidth}
  on:wheel={handleWheel}
  on:pointerdown={(e) => {
    if (e.button !== 0) {
      return;
    }

    pointerStart = { x: e.clientX, y: e.clientY };
  }}
  on:pointermove={(e) => {
    if (movedClip) {
      const delta = e.clientX - pointerStart.x;
      let start = movedClip.start + delta / secWidth;

      if (!e.altKey) {
        start = Math.round(start / snap) * snap;
      }

      start = Math.max(-movedClip.clip.extentPast, start);
      movedClip.clip.start = start;

      $project.tracks = $project.tracks;
    }
  }}>
  <VStack spacing={4}>
    {#each $project.tracks as track}
      <PlaylistTrack
        bind:track
        viewRegion={$animatedViewRegion}
        {secWidth}
        {snap}
        on:pointerenter={() => {
          if (movedClip && track.mod.canPlay(movedClip.clip)) {
            movedClip.track.remove(movedClip.clip);
            track = track.insert(movedClip.clip);
            movedClip.track = track;
          }
        }}
        on:cliptake={(e) => {
          movedClip = { clip: e.detail, start: e.detail.start, track };
        }} />
    {/each}
  </VStack>
  <div class="cursor" style={`--x: ${cursorPos}px`} />
</div>
