<script lang="ts" context="module">export function grow(node, amount = 1) {
    node.style.flexGrow = `${amount}`;
}
export function basis(node, basis) {
    node.style.flexBasis = basis;
}
</script>

<script lang="ts">export let dir = "horizontal";
export let align = "stretch";
export let spacing = 0;
export let hpad = 0;
export let vpad = 0;
export let inline = false;
export let reverse = false;
</script>

<style lang="scss">.stack {
  display: flex;
}
.stack.inline {
  display: inline-flex;
}
.stack.horizontal {
  flex-direction: row;
}
.stack.horizontal.reverse {
  flex-direction: row-reverse;
}
.stack.vertical {
  flex-direction: column;
}
.stack.vertical.reverse {
  flex-direction: column-reverse;
}</style>

<div
  class="stack"
  class:inline
  class:reverse
  class:horizontal={dir === 'horizontal'}
  class:vertical={dir === 'vertical'}
  style={`
    padding: ${vpad}px ${hpad}px;
    gap: ${spacing}px;
    align-items: ${{ start: 'flex-start', center: 'center', end: 'flex-end', stretch: 'stretch' }[align]};
  `}>
  <slot />
</div>
