<script lang="ts">export let position;
export let factor = 1;
export let direction = "column";
export let reverse = false;
export let snaps = undefined;
export let snapdist = 10;
export let min = undefined;
export let max = undefined;
export let color = "var(--color-foreground-2)";
$: if (min) {
    position = Math.max(min, position);
}
$: if (max) {
    position = Math.min(max, position);
}
$: if (snaps) {
    const candidates = snaps.filter((snap) => Math.abs(position - snap) < snapdist);
    if (candidates.length) {
        const closest = candidates.reduce((closestYet, snap) => {
            if (Math.abs(snap - position) < Math.abs(closestYet - position)) {
                return snap;
            }
            return closestYet;
        });
        position = closest;
    }
}
function handlePointerDown(e) {
    if (e.button !== 0) {
        return;
    }
    const revFactor = reverse ? -1 : 1;
    const startPos = position;
    const pointerStart = { x: e.clientX, y: e.clientY };
    this.onpointermove = (e) => {
        let delta;
        if (direction === "column") {
            delta = (e.clientY - pointerStart.y) * factor * revFactor;
        }
        else if (direction === "row") {
            delta = (e.clientX - pointerStart.x) * factor * revFactor;
        }
        position = startPos + delta;
    };
    this.onpointerup = (e) => {
        this.onpointermove = null;
        this.onpointerup = null;
        this.releasePointerCapture(e.pointerId);
    };
    this.setPointerCapture(e.pointerId);
}
</script>

<style lang="scss">.split-bar {
  display: inline-block;
  position: relative;
  --hotspot-size: 8px;
}
.split-bar .hotspot {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.split-bar .hotspot::before {
  content: "";
  --distance-to-edge: 8px;
  --line-width: 2px;
  position: absolute;
  opacity: 0;
  border-radius: calc(var(--line-width) / 2);
  transition: opacity var(--anim-short);
  background: currentColor;
}
.split-bar .hotspot:hover::before {
  opacity: 0.5;
}
.split-bar .hotspot.horizontal {
  cursor: ew-resize;
  width: var(--hotspot-size);
  transform: translateX(-50%);
}
.split-bar .hotspot.horizontal::before {
  left: calc(50% - var(--line-width) / 2);
  width: var(--line-width);
  top: var(--distance-to-edge);
  bottom: var(--distance-to-edge);
}
.split-bar .hotspot.vertical {
  cursor: ns-resize;
  height: var(--hotspot-size);
  transform: translateY(-50%);
}
.split-bar .hotspot.vertical::before {
  top: calc(50% - var(--line-width) / 2);
  height: var(--line-width);
  left: var(--distance-to-edge);
  right: var(--distance-to-edge);
}</style>

<div class="split-bar">
  <div
    class="hotspot"
    class:horizontal={direction === 'row'}
    class:vertical={direction === 'column'}
    style={`color: ${color}`}
    on:pointerdown|stopPropagation={handlePointerDown} />
</div>
