<script lang="ts">;
import { createEventDispatcher } from "svelte";
import { AudioClip } from "@api/audio";
import AudioWaveform from "./AudioWaveform.svelte";
export let clip;
export let visibleRange;
export let snap;
export let secWidth;
export let color = "var(--color-red)";
var ResizeSide;
(function (ResizeSide) {
    ResizeSide["Start"] = "start";
    ResizeSide["End"] = "end";
})(ResizeSide || (ResizeSide = {}));
const dispatch = createEventDispatcher();
function resizer(node, side) {
    function handlePointerDown(e) {
        if (e.button !== 0) {
            return;
        }
        e.stopPropagation();
        e.preventDefault();
        const start = {
            x: e.clientX,
            timePoint: side === ResizeSide.Start ? clip.extentPast : clip.extent,
            extent: clip.extent,
        };
        const pointermove = (e) => {
            const dt = (e.clientX - start.x) / secWidth;
            let t = start.timePoint + dt;
            if (!e.altKey) {
                t = Math.round(t / snap) * snap;
            }
            if (side === ResizeSide.Start) {
                clip.extentPast = Math.max(Math.min(t, 0), -clip.start);
            }
            else {
                clip.extent = Math.max(t, 0);
            }
        };
        const pointerup = (e) => {
            this.removeEventListener("pointermove", pointermove);
            this.removeEventListener("pointerup", pointerup);
            this.releasePointerCapture(e.pointerId);
        };
        this.addEventListener("pointermove", pointermove);
        this.addEventListener("pointerup", pointerup);
        this.setPointerCapture(e.pointerId);
    }
    node.addEventListener("pointerdown", handlePointerDown);
    return {
        update(newSide) {
            side = newSide;
        },
        destroy() {
            node.removeEventListener("pointerdown", handlePointerDown);
        },
    };
}
</script>

<style lang="scss">.clip {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}
.clip .content {
  display: flex;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
}
.clip .content::before, .clip .content::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: inherit;
}
.clip .content::before {
  background: var(--color-background-2);
}
.clip .content::after {
  border: 1px solid var(--color-foreground-2);
  opacity: 0.5;
}
.clip .content .data {
  flex-shrink: 0;
  background: var(--color-background-0);
}
.clip .resize-handle {
  flex-basis: 0;
  position: relative;
  width: 0px;
  cursor: ew-resize;
}
.clip .resize-handle::after {
  content: "";
  position: absolute;
  top: 0;
  left: -4px;
  bottom: 0;
  width: 8px;
  z-index: 1;
}</style>

<div
  on:pointerdown={(e) => {
    if (e.button !== 0) {
      return;
    }

    dispatch('cliptake', clip);
  }}
  class="clip"
  style={`
    --clip-color: ${color};
    transform: translateX(${(clip.start + clip.extentPast) * secWidth}px);
  `}>
  <div class="resize-handle" use:resizer={'start'} />
  <div
    class="content"
    style={`width: ${Math.max(clip.totalExtent * secWidth, 1)}px`}>
    <div
      class="data"
      style={`
        transform: translateX(${-clip.extentPast * secWidth}px);
        width: ${clip.length * secWidth}px
      `}>
      {#if clip instanceof AudioClip}
        <AudioWaveform blob={clip.blob} {visibleRange} />
      {/if}
    </div>
  </div>
  <div class="resize-handle" use:resizer={'end'} />
</div>
